// @flow
import React, { Component } from 'react';
import { observer } from 'mobx-react';
import Bullet from "../lib/Bullet"
import { metersToPixel } from "../lib/TickUtils"
import { gameParams } from "../lib/gameInfo"
import Victor from "victor"

@observer
class BulletView extends Component {
    props: {
        bullet: Bullet
    }
    render() {
        const { sourceCoordinate: { x: sourceX, y: sourceY }, currentCoordinate: { x, y }, displayOffset: { x: offsetX, y: offsetY } } = this.props.bullet
        let s = new Victor(sourceX, sourceY)
        let t = new Victor(x, y)
        let offsetFactor = s.distance(t) / metersToPixel(gameParams.mediumCircleRadius)
        return (
            <div 
            className="bullet" 
            style={{ transform: "translate(" + (Math.round(x + (offsetX * offsetFactor))) + "px, " + (Math.round(y + (offsetFactor * offsetY))) + "px)" }}>
            </div>
        )
        // return ( 
        //     <svg width="10" height="10" x={x} y={x} fill="white">
        //     </svg>
        // )
    }
}

export default BulletView



