// @flow
import React, { Component } from 'react';
import PropTypes from "prop-types";
import { observer } from 'mobx-react';
import { autorun } from "mobx"

import DistanceCircles from "./DistanceCircles"
import UnitView from "./UnitView"
import BulletView from "./BulletView"
import Line from "./Line"

import { teams } from "../lib/gameInfo"
import Board from "../lib/Board"
import CanvasBullets from "./CanvasBullets"

@observer
class BoardView extends Component {

    props: {
        board: Board,
        children: PropTypes.element.isRequired,
        clickOnUnit: mixed
    }

    // componentDidUpdate() {
    //     this.refs.canvas.forceUpdate();
    // }

    render() {
        const { children, clickOnUnit, board: { width, height, units, bullets } } = this.props
        return (
            [
                <div className="board" style={{ width: width, height: height }}>
                    {children}
                    <CanvasBullets ref="canvas" width={width} height={height} bullets={bullets} />
                    <svg height={height} width={width} style={{ position: "absolute" }}>
                        {
                            units.map(unit =>
                                <Line
                                    key={unit.id}
                                    unit={unit}
                                    offset={unit.team === teams.red ? 3 : -3}
                                    color={unit.team.colour}
                                />
                            )
                        }
                    </svg>
                    {units.map(unit => <DistanceCircles key={unit.id} unit={unit} />)}
                    {units.map(unit => <UnitView key={unit.id} unit={unit} clickOnUnit={clickOnUnit} />)}
                </div>
            ]
        )
    }
}


export default BoardView



