// @flow
import React, { Component } from 'react';
import { observer } from 'mobx-react';
import Unit from "../lib/Unit"

@observer
export default class Line extends Component {

  props: {
    unit: Unit,
    offset: number,
    color: string
  }

  render() {
    const { unit, offset, color } = this.props;
    let targetCoordinates = unit.targetCoordinates;
    if (!unit.targetCoordinates) return null;
    return ( 
      <line 
        className="line"
        x1={ unit.x + offset } 
        x2={ targetCoordinates.x + offset} 
        y1={ unit.y } 
        y2={ targetCoordinates.y + offset} 
        stroke={ color } 
        strokeWidth="2"  
        strokeDasharray="2, 6"
        />
    )
  }
}



