// @flow
import React, { Component } from 'react'
import { observer } from 'mobx-react'
import './App.css'
import _ from 'lodash'

import BoardView from "./components/BoardView"
import GameControls from "./components/GameControls"
import Fps from "./components/Fps"

import Unit from "./lib/Unit"
import Board from "./lib/Board"
import Game from "./lib/Game"
import { unitTypes, teams, actionTypes, directions, distances } from "./lib/gameInfo"
import type { UnitAction } from "./lib/GameTypes"
import { distanceType } from "./lib/TickUtils"

import DevTools from 'mobx-react-devtools';

const ais = {
    // A simple AI to get the units moving
    verySimpleAi: {
        findNextAction: function (unit: Unit, board: Board) {
            // pick random enemy unit
            let newTarget: Unit = _.sample(board.units.slice().filter(u => u !== unit && u.team !== unit.team))

            // randonly pick move or attack action
            let currentAction: UnitAction;
            if (Math.random() <= 0.5) {
                currentAction = {
                    target_unit: newTarget,
                    actionType: actionTypes.move,
                    direction: _.sample(directions),
                }
            } else {
                currentAction = {
                    target_unit: newTarget,
                    actionType: actionTypes.attack,
                }
            }
            return currentAction;
        }
    }, 
    simpleAi: {
        findNextAction: function(unit: Unit, board: Board) {
            const oldAction = unit.currentAction

            let otherUnits =  board.units.slice().filter(u => u !== unit && u.team !== unit.team)
            let targetUnitExists = _.includes(otherUnits, oldAction.target_unit)
            let distanceToTargetUnit = unit.distance(oldAction.target_unit)

            let closestAttackingMe
            let distanceToClosestAttacking
            let unitsAttackingMe = otherUnits.filter(
                    u => u.currentAction.target_unit === unit && u.currentAction.actionType == actionTypes.attack)
            if (unitsAttackingMe.length > 0) {
                closestAttackingMe = _.minBy(unitsAttackingMe, u => unit.absoluteDistance(unit))
                distanceToClosestAttacking = unit.distance(closestAttackingMe)
            }

            let closestTarget = _.minBy(otherUnits, u => unit.absoluteDistance(u))
            let closestTargetDistance = unit.distance(closestTarget)

            let currentAction: UnitAction;
            if (distanceToClosestAttacking && distanceToClosestAttacking != distances.outOfRange && unit.shield / unit.unitType.shield <= 0.25) {
                currentAction = {
                    target_unit: closestAttackingMe,
                    actionType: actionTypes.move,
                    direction: directions.away
                }
            } 
            else if (targetUnitExists && oldAction.actionType === actionTypes.attack && 
                    distanceToTargetUnit != distances.outOfRange &&
                   (unit.unitType == unitTypes.sniper || distanceToTargetUnit != distances.long)) {
                if (distanceToTargetUnit === distances.short || distanceToTargetUnit === distances.medium) {
                    // Keep attacking selfAttacking if at short/medium
                    currentAction = oldAction
                } 
                else if (unit.unitType === unitTypes.sniper && distanceToTargetUnit === distances.long) {
                    currentAction = oldAction
                } else if (closestTargetDistance === distances.short || closestTargetDistance === distances.medium) {
                    currentAction = {
                        target_unit: closestTarget,
                        actionType: actionTypes.attack,
                    }
                } else if (distanceToTargetUnit == distances.long) {
                    currentAction = oldAction
                }
            } else {
                if (closestTargetDistance === distances.short || closestTargetDistance === distances.medium) {
                    currentAction = {
                        target_unit: closestTarget,
                        actionType: actionTypes.attack,
                    }
                } else if (unit.unitType === unitTypes.sniper && closestTargetDistance === distances.long) { 
                    currentAction = {
                        target_unit: closestTarget,
                        actionType: actionTypes.attack,
                    }
                } else {
                    currentAction = {
                        target_unit: closestTarget,
                        actionType: actionTypes.move,
                        direction: directions.towards,
                    }
                }
            }
            return currentAction
        }

    }
}

const levelConfig = {
    // unit positions, types and selected AIs
    units: [
        { ai: ais.simpleAi, team: teams.red, type: unitTypes.assault, x: 0, y: 0 },
        { ai: ais.simpleAi, team: teams.red, type: unitTypes.machineGun, x: 0, y: 100 },
        { ai: ais.simpleAi, team: teams.red, type: unitTypes.shotgun, x: 0, y: 200 },
        { ai: ais.simpleAi, team: teams.red, type: unitTypes.sniper, x: 0, y: 300 },
        { ai: ais.simpleAi, team: teams.red, type: unitTypes.assault, x: 0, y: 400 },
        { ai: ais.simpleAi, team: teams.blue, type: unitTypes.sniper, x: 500, y: 0 },
        { ai: ais.simpleAi, team: teams.blue, type: unitTypes.shotgun, x: 500, y: 100 },
        { ai: ais.simpleAi, team: teams.blue, type: unitTypes.assault, x: 500, y: 200 },
        { ai: ais.simpleAi, team: teams.blue, type: unitTypes.assault, x: 500, y: 300 },
        { ai: ais.simpleAi, team: teams.blue, type: unitTypes.machineGun, x: 500, y: 400 },
    ]
}

@observer
class App extends Component {

    props: {}
    state: {
        game: Game
    }

    constructor(props: Object) {
        super(props);
        this.state = {
            game: this.newGame()
        }
    }

    render() {
        const { game, game: { board } } = this.state;
        return (
            <div className="App">
                <BoardView board={board} clickOnUnit={game.clickOnUnit}>
                    <Fps>TargetFPS: {game.targetFps}  FPS: { game.fps }</Fps>
                    <GameControls fps={game.fps} game={game} reset={this.reset} />
                </BoardView>
            </div>
        );
    }

    reset = () => {
        this.setState({
            game: this.newGame()
        })
    }

    newGame(): Game {
        return new Game(levelConfig)
    }

    // Resize the board when the screen is resized (mobile changed from portrait to landscape)
    componentDidMount() {
        this.updateWindowDimensions();
        window.addEventListener('resize', () => this.updateWindowDimensions());
    }

    componentWillUnmount() {
        window.removeEventListener('resize', () => this.updateWindowDimensions());
    }

    updateWindowDimensions() {
        // ewww
        this.state.game.board.width = window.innerWidth
        this.state.game.board.height = window.innerHeight
    }
}

export default App;



