// @flow
import React, { Component } from 'react';
import { observer } from 'mobx-react';

import CircularBar from "./CircularBar"
import Bar from "./Bar"
import Unit from "../lib/Unit"
import { distances } from "../lib/gameInfo"

@observer
class UnitView extends Component {

    props: {
        unit: Unit,
        clickOnUnit: any
    }

    clickOnUnit = () => {
        const { clickOnUnit, unit } = this.props
        clickOnUnit(unit)
    }

    render() {
        const {
            unit,
            unit: {
                x, y, team, shieldPercentage, shootingLevel, hpPercentage, rotation,
                unitType: { short, hp: hpMax, shield: shieldMax }
            }
        } = this.props;
        let classNames = ["unit"]
        if (unit.distanceToSelectedUnit !== 0) {
            switch(unit.distanceToSelectedUnit) {
                case distances.outOfRange:
                    break;
                case distances.short:
                    classNames.push("short")
                    break;
                case distances.medium:
                    classNames.push("medium")
                    break;
                case distances.long:
                    classNames.push("long")
                    break;
            }
        }
        return (
            <div
                className={classNames.join(" ")}
                style={{ transform: "translate(" + x + "px, " + y + "px)" }}
                onClick={this.clickOnUnit}>
                <div className="unitStatus">
                    <div>
                        <Bar color="white" percent={shieldPercentage} maxLevel={shieldMax} />
                        <Bar color={unit.team.colour} percent={hpPercentage} maxLevel={hpMax} />
                    </div>
                    <div style={{ paddingLeft: 5, width: 12, height: 12 }}>
                        <CircularBar percentage={shootingLevel} />
                    </div>
                </div>
                <div style={{ paddingRight: "15px" }}>
                    <div className={"unit-body " + team.class}>
                        {short}
                    </div>
                </div>
            </div>
        )
    }
                    //<div className={"unit-body " + team.class} style={{ transform: "rotate(" + rotation + "deg) " }} >
                    // <CircularProgressbar percentage={shootingLevel} strokeWidth="25" />
}

export default UnitView



