import React, { Component } from 'react';
import { observer } from 'mobx-react';

var HEIGHT = 12;
var STROKEWIDTH = 2
var RADIUS = (HEIGHT - (2 * STROKEWIDTH)) / 2;
var CIRCUMFERENCE = 2 * Math.PI * RADIUS; 

@observer
export default class CircularBar extends Component {

    render() {
        const { percentage=0 } = this.props
        var progress = percentage / 100;
        var dashoffset = CIRCUMFERENCE * (1 - progress); 
        return (
            <svg className="progress" width={HEIGHT} height={HEIGHT}>
                <circle 
                    cx={HEIGHT / 2} 
                    cy={HEIGHT / 2} 
                    r={(HEIGHT / 2) - STROKEWIDTH} 
                    fill="none" 
                    stroke="#444444" 
                    strokeWidth="2" />
                <circle 
                    strokeDashoffset={dashoffset}
                    strokeDasharray={CIRCUMFERENCE}
                    className="progress__value" 
                    cx={HEIGHT / 2} 
                    cy={HEIGHT / 2} 
                    r={(HEIGHT / 2) - STROKEWIDTH} 
                    fill="none" 
                    stroke="orange" 
                    strokeWidth="2" />
            </svg>
        )
    }
}



